<template>
  <view class="container">
    <view class="tabbar">
      <view
        class="tab-item"
        :class="{ 'active': currentTab === 0 }"
        @click="switchTab(0)"
      >
        A
      </view>
      <view
        class="tab-item"
        :class="{ 'active': currentTab === 1 }"
        @click="switchTab(1)"
      >
        B
      </view>
      <view
        class="tab-item"
        :class="{ 'active': currentTab === 2 }"
        @click="switchTab(2)"
      >
        C
      </view>
    </view>
    <swiper :current="currentTab" @change="swiperChange" style="height: 100rpx;">
      <swiper-item class="sub-tab">
        <view
          class="sub-tab-item"
          :class="{ 'active': currentSubTab === 0 }"
          @click="switchSubTab(0)"
        >
          A1
        </view>
        <view
          class="sub-tab-item"
          :class="{ 'active': currentSubTab === 1 }"
          @click="switchSubTab(1)"
        >
          A2
        </view>
		
		<view v-if="currentTab === 0">
		  <view v-if="currentSubTab === 0">A1 Content</view>
		  <view v-else>A2 Content</view>
		</view>
		
      </swiper-item>
	  
	  
	  
      <swiper-item class="sub-tab">
        <view
          class="sub-tab-item"
          :class="{ 'active': currentSubTab === 0 }"
          @click="switchSubTab(0)"
        >
          B1
        </view>
        <view
          class="sub-tab-item"
          :class="{ 'active': currentSubTab === 1 }"
          @click="switchSubTab(1)"
        >
          B2
        </view>
		
		<view v-if="currentTab === 1">
		  <view v-if="currentSubTab === 0">B1 Content</view>
		  <view v-else>B2 Content</view>
		</view>
		
      </swiper-item>
      <swiper-item class="sub-tab">
        <view 
          class="sub-tab-item"
          :class="{ 'active': currentSubTab === 0 }"
          @click="switchSubTab(0)"
        >
          C1
        </view>
        <view 
          class="sub-tab-item"
          :class="{ 'active': currentSubTab === 1 }"
          @click="switchSubTab(1)"
        >
          C2
        </view>
		
		<view v-if="currentTab === 2">
		  <view v-if="currentSubTab === 0">C1 Content</view>
		  <view v-else>C2 Content</view>
		</view>
      </swiper-item>
    </swiper>
	
	
  </view>
</template>

<script>
export default {
  data() {
    return {
      currentTab: 0,
      currentSubTab: 0
    }
  },
  methods: {
    switchTab(index) {
      this.currentTab = index
    },
    swiperChange(e) {
      this.currentTab = e.detail.current
    },
    switchSubTab(index) {
      this.currentSubTab = index
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  width: 100%;
  height: 100vh;
  .tabbar {
    display: flex;
    justify-content: space-around;
    .tab-item {
      padding: 10rpx 0;
      &.active {
        color: #007AFF;
        border-bottom: 2px solid #007AFF;
      }
    }
  }
  .sub-tab {
    display: flex;
    .sub-tab-item {
      padding: 20rpx 30rpx;
      &.active {
        color: #007AFF;
      }
    }
  }
}
</style>